"use client";
import { useEffect, useMemo, useState } from "react";

import Label from "@/components/form/Label";
import Input from "@/components/form/input/InputField";
import Select from "@/components/form/Select";
import TextArea from "@/components/form/input/TextArea";
import Button from "@/components/ui/button/Button";

export type ReservationFormDefaults = {
  memberId?: string;     // "1"
  classId?: string;      // "101"
  date?: string;         // YYYY-MM-DD
  status?: string;       // confirmed | waitlist | cancelled | no-show
  notes?: string;
};

type Props = {
  defaultValues?: ReservationFormDefaults;
  primaryButtonLabel?: string;
};

function normalizeDefaults(d?: ReservationFormDefaults): Required<ReservationFormDefaults> {
  return {
    memberId: d?.memberId ?? "",
    classId: d?.classId ?? "",
    date: d?.date ?? "",
    status: d?.status ?? "",
    notes: d?.notes ?? "",
  };
}

export default function AddReservationForm({
  defaultValues,
  primaryButtonLabel = "Save Reservation",
}: Props) {
  // Mock options (después lo conectamos con backend)
  const memberOptions = [
    { value: "1", label: "Lindsey Curtis (lindsey@gmail.com)" },
    { value: "2", label: "Kaiya George (kaiya@gmail.com)" },
    { value: "3", label: "Zain Geidt (zain@gmail.com)" },
  ];

  const classOptions = [
    { value: "101", label: "CrossFit — Mon 18:00" },
    { value: "102", label: "Open Box — Mon 19:00" },
    { value: "103", label: "Weightlifting — Tue 18:00" },
  ];

  const statusOptions = [
    { value: "confirmed", label: "Confirmed" },
    { value: "waitlist", label: "Waitlist" },
    { value: "cancelled", label: "Cancelled" },
    { value: "no-show", label: "No-show" },
  ];

  const initial = useMemo(() => normalizeDefaults(defaultValues), [defaultValues]);
  const [form, setForm] = useState<Required<ReservationFormDefaults>>(initial);

  useEffect(() => {
    setForm(normalizeDefaults(defaultValues));
  }, [defaultValues]);

  return (
    <div className="space-y-6">
      <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
        <div className="border-b border-gray-200 px-6 py-4 dark:border-gray-800">
          <h2 className="text-lg font-medium text-gray-800 dark:text-white">
            Reservation Details
          </h2>
        </div>

        <div className="p-4 sm:p-6 dark:border-gray-800">
          <form>
            <div className="grid grid-cols-1 gap-5 md:grid-cols-2">
              <div>
                <Label>Member</Label>
                <Select
                  options={memberOptions}
                  placeholder="Select member"
                  defaultValue={form.memberId}
                  onChange={(value) => setForm((p) => ({ ...p, memberId: value }))}
                />
              </div>

              <div>
                <Label>Class</Label>
                <Select
                  options={classOptions}
                  placeholder="Select class"
                  defaultValue={form.classId}
                  onChange={(value) => setForm((p) => ({ ...p, classId: value }))}
                />
              </div>

              <div>
                <Label>Date</Label>
                <Input
                  type="date"
                  value={form.date}
                  onChange={(e) => setForm((p) => ({ ...p, date: e.target.value }))}
                />
              </div>

              <div>
                <Label>Status</Label>
                <Select
                  options={statusOptions}
                  placeholder="Select status"
                  defaultValue={form.status}
                  onChange={(value) => setForm((p) => ({ ...p, status: value }))}
                />
              </div>

              <div className="col-span-full">
                <Label>Notes</Label>
                <TextArea
                  rows={6}
                  placeholder="Notes (optional)"
                  value={form.notes}
                  onChange={(e) => setForm((p) => ({ ...p, notes: e.target.value }))}
                />
              </div>
            </div>
          </form>
        </div>
      </div>

      <div className="flex flex-col gap-3 sm:flex-row sm:justify-end">
        <Button variant="outline">Cancel</Button>
        <Button variant="primary">{primaryButtonLabel}</Button>
      </div>
    </div>
  );
}
